/* PomodoroTimer.module.css */
*:after,
*:before {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  text-decoration: none;
}
:root {
  --yellow: #e49400;
  --green: #2d8515;
  --red: #db2a34;
  --blue: #2477ff;
  --dark-blue: #17193b;
  --purple: #474d84;
  --purple-secondary: #333867;
  --title-primary-color: rgb(231 226 226);
}
/* .pomodoroTimer {
  height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
} */

.container {
  display: flex;
  flex-direction: row;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  height: 620px;
  border-radius: 20px;
  background: var(--dark-blue);
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.cardHeader {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
}

.cardHeader h1 {
  font-weight: 900;
  font-size: 24px;
  color: var(--title-primary-color);
}

.cardHeaderButtons {
  display: flex;
  justify-content: stretch;
  gap: 10px;
  padding: 4px;
}

.cardHeaderButtons button {
  padding: 8px;
  border-radius: 20px;
  font-weight: 700;
  font-size: 12px;
  background-color: var(--purple);
  color: var(--title-primary-color);
}

.cardHeaderButtons .active {
  background-color: var(--red);
}

.cardBody {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 30px;
}

.progressBar {
  height: 180px;
  width: 180px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: conic-gradient(var(--blue) 360deg, var(--purple) 0deg);
  border-radius: 50%;
  animation: pulse 3s infinite;
  transition: all ease;
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
    rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
    rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
}

.progressBarInner {
  width: 90%;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--dark-blue);
  border-radius: 50%;
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
    rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
    rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
}

.progressBarInner h2 {
  color: var(--title-primary-color);
  font-weight: 900;
  font-size: 40px;
}

.cardFooter {
  display: flex;
  justify-content: stretch;
  gap: 20px;
  padding: 10px;
}

.cardFooter button {
  padding: 10px 12px;
  border-radius: 6px;
  background-color: var(--yellow);
  color: var(--title-primary-color);
  font-weight: 900;
  font-size: 14px;
}

.cardFooter .btnSuccess {
  background-color: var(--green);
}

.cardFooter .btnDanger {
  background-color: var(--red);
}

@keyframes pulse {
  0% {
    transform: scale(0.97);
    box-shadow: 0 0 0 0 rgba(52, 172, 224, 0.7);
  }
  50% {
    transform: scale(1);
    box-shadow: 0 0 0 10px rgba(52, 172, 224, 0);
  }
  100% {
    transform: scale(0.97);
    box-shadow: 0 0 0 0 rgba(52, 172, 224, 0);
  }
}
